import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

import { BaseComponent } from '../../../shared/components/base.component';
import { AppService } from '../../../services/app.service';
import { ActivatedRoute } from '@angular/router';

import * as moment from 'moment';
import { IBooking } from '../../../services/data/bookings.service';

@Component({
    selector: 'schedule-view-event',
    templateUrl: './view-event.component.html',
    styleUrls: ['./view-event.component.scss']
})
export class ScheduleViewEventComponent extends BaseComponent implements OnInit {
    @Output() public loading = new EventEmitter();

    public model: any = {};

    constructor(private service: AppService, private route: ActivatedRoute) {
        super();
    }

    public ngOnInit() {
        this.subs.obs.query = this.route.queryParamMap.subscribe((params) => {
            if (params.has('id')) {
                if (params.has('date')) { this.model.date = params.get('date'); }
                if (params.has('duration')) { this.model.duration = params.get('duration'); }
                this.model.event_id = params.get('id');
                this.loadEvent();
            }
        });
    }

    public loadEvent() {
        if (!this.model.event_id) {
            return this.service.navigate('schedule');
        }
        this.loading.emit('Loading event data...');
        const start = moment(this.model.date || '').add(-5, 'm');
        const end = moment(start).add((this.model.duration || 120) + 10, 'm');
        this.service.Bookings.query({ from: start.unix(), to: end.unix() }).then((list) => {
            for (const bkn of list) {
                if (bkn.id === this.model.event_id) {
                    this.model.event = bkn;
                    break;
                }
            }
            this.loading.emit(false);
            if (!this.model.event) {
                this.service.error(`Failed to load booking with ID "${this.model.event_id}"`);
                return this.service.navigate('schedule');
            }
            this.generateLink();
        }, () => {
            this.loading.emit(false);
            this.service.error(`Failed to load booking with ID "${this.model.event_id}"`);
            this.service.navigate('schedule');
        });
    }

    public cancel() {
        this.service.navigate(['schedule'], {});
    }

    public generateLink() {
        let emails = ``;
        for (const user of this.model.event.attendees) {
            if (emails) { emails += ', '; }
            emails += user.email;
        }
        const title = encodeURIComponent(this.model.event.title);
        const body = encodeURIComponent(this.model.event.title);
        const org_email = (this.model.event as IBooking).organiser.email;
        this.model.link = `mailto:${org_email}?cc=${emails}&subject=Re:${title}`;
    }

}
