import { Component, OnInit, ViewChild } from '@angular/core';
import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';
import { BehaviorSubject } from 'rxjs';

import { BaseComponent } from '../../../shared/components/base.component';
import { AppService } from '../../../services/app.service';
import { IBooking } from '../../../services/data/bookings.service';

import * as moment from 'moment';

@Component({
    selector: 'schedule-event-list',
    templateUrl: './event-list.component.html',
    styleUrls: ['./event-list.component.scss']
})
export class ScheduleEventListComponent extends BaseComponent implements OnInit {

    public model: any = {};
    public items: BehaviorSubject<IBooking[]> = new BehaviorSubject([]);

    @ViewChild(CdkVirtualScrollViewport) private viewport: CdkVirtualScrollViewport;

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        this.model.days = 3;
        this.service.Bookings.listen('list', (bkns) => {
            this.model.list = bkns;
            this.processBookings();
        });
        this.atBottom();
    }

    public processBookings() {
        this.model.list.sort((a, b) => a.date - b.date);
        const list: any[] = [];
        const now = moment();
        const date = moment();
        const end = moment().add(this.model.days, 'd');
        for (; date.isSameOrBefore(end, 'd'); date.add(1, 'd')) {
            list.push({ type: 'date', today: now.isSame(date, 'd'), title: date.format('dddd, DD MMM') });
            const length = list.length;
            for (const bkn of this.model.list) {
                const bkn_date = moment(bkn.date);
                if (bkn_date.isSame(date, 'd')) {
                    list.push({ ...bkn, type: 'event' });
                }
            }
            if (list.length === length) {
                list.push({ type: 'no-items', title: 'No events' });
            }
        }
        console.log('List:', list);
        this.items.next(list);
    }

    public atBottom() {
        this.timeout('at_bottom', () => {
            if (!this.viewport) { return this.timeout('atBottom', () => this.atBottom()); }
            const end = this.viewport.getRenderedRange().end;
            const total = this.viewport.getDataLength();

            if (end === total) {
                const date = moment().hours(0).minutes(0).add(this.model.days + 1, 'd');
                this.model.days += 3;
                this.model.loading = true;
                this.service.Bookings.query({ from: date.unix(), to: date.add(3, 'd').unix() })
                    .then(() => {
                        this.processBookings();
                        this.model.loading = false;
                    }, () => this.model.loading = false);
            }
        }, 100);
    }

    public view(item) {
        if (item.id) {
            this.service.navigate(['schedule', 'event'], {
                id: item.id,
                date: item.date,
                duration: item.duration
            });
        }
    }
}
